import React,{useState,useEffect} from "react";
import "../stylesheet/profile.css"
import firebase from "./Firebase";
// import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
// import { faCheckSquare, faCoffee} from '@fortawesome/fontawesome-free-solid'


const Profile = () => {

        const [name, setName]=useState();
        const [surname, setSurname]=useState();
        const [age, setAge]=useState();
        const [location, setLocation]=useState();
        const [reg, setReg]=useState([]);

        const db = firebase.firestore();

        const getname =(e)=>{
            setName(e.target.value)
        }

        const getsurname=(e)=>{
            setSurname(e.target.value)
        }

        const getage=(e)=>{
            setAge(e.target.value)
        }

        const getlocation=(e)=>{
            setLocation(e.target.value);
        }

        // function to receive data
        const submitInfo = (e) =>{
            e.preventDefault();
            db.collection("users").add(
                {
                    name: name,
                    surname: surname,
                    age : age,
                    location: location
                }
            ).then((res)=>{
                console.log('user successfully registered')
            }).catch((err)=>{
                console.log(err)
            })
            //AddUsersInfo(name,surname,age,location)
        }

        // getting all the users from the database

        useEffect(()=>{

            // variable to store user data
            let info = [];

            db.collection("users").get()
            .then((res)=>{
                res.forEach(action=>{
                    info.push({...action.data(), id:action.id});
                    console.log(action.data)
                })
                setReg(info);
            },[]
            )
        })

        const deleteUser=(e)=>{

            let uid =e.target.id

            db.collection('users').doc(uid).delete()
            .then(()=>{
                console.log("user successfully deleted")
                
            }).catch((err)=>{
                console.log(err)
            }
            )
        }

        // update user information
        const updateUser=(e)=>{
            let uid = e.target.id

            db.collection('users').doc(uid).update({
                name: name,
                surname: surname,
                location: location,
                age: age
            }).then(()=>{
                console.log('user updated')
            })
        }


        return (
            <>
                <div className="prof-container">

                    <div className="prof-right">

                        <h5 className="reg-txt">Register Account</h5>

                        <form className="form-wrapper" onSubmit={submitInfo}>

                            <input type="text" placeholder="First-Name" className="input-field"
                               name="name" onChange={getname}
                            />
                            
                            <input type="text" placeholder="Description" className="input-field"
                                name="description" onChange={getsurname}
                            />
                            
                            <input type="text" placeholder="Age" className="input-field"
                            name="age" onChange={getage}
                            />

                            <input type="text" placeholder="Location" className="input-field"
                            name="location" onChange={getlocation}
                            />

                            <div className="submit">
                                <button  type="submit">Register</button>
                            </div>

                        </form>
                        {
                            reg.map((action) => (

                                <div class="card" key={action.id}>
                            
                                    <h1>User's information</h1>

                                    <p class="price"><a href={'./landing'+action.id}> {action.name} </a> </p>   
                                   

                                    <p>Description: {action.surname}</p>
                                    <p>Age: {action.age}</p>
                                    <p>Location: {action.location}</p>
                                    

                                    <div className="btns">

                                        <button id={action.id} onClick={
                                            deleteUser
                                        }>Delete</button>   

                                        <button id={action.id} onClick={updateUser}>Update</button>

                                    </div>
                                    
                                </div>
                            ))
                        }

                    </div>
                        

                </div>
            </>
        )
    }
export default Profile;